.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}


.todo-wrapper {
  flex: 1; 
}

.todo-list {
  padding: 30px;
  display: grid;
  grid-gap: 8px; 
}


.header, .footer {
  background: peachpuff;
  height: 100px;
}

.header {
  font-size: 2rem;
}


.footer {
  font-size: .8rem;
}